







































































































































.card-widget {
  position: relative;
  display: flex;
  flex-direction: column;
  > .uk-card-header {
    padding: 0;
    border-bottom: 0 none;
    + .uk-card-body {
      padding-top: 4px;
    }
  }
  > .uk-card-body {
    flex: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    border: 1px solid rgba(255,255,255,0.25);
    border-left-color: rgba(255,255,255,0.1);
    border-right-color: rgba(255,255,255,0.15);
    .content {
      flex: 1;
    }
  }
  > .uk-card-footer {
    padding: 0;
    border-top: 0 none;
  }
  .model-name {
    padding: 4px;
    color: cadetblue;
  }
}
